import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

import './App.css';
import { AppProvider } from './components/AppContext';
import { Question, questions } from '@/constants';

import "./fonts/Font.css";

function App() {
  const navigate = useNavigate(); // useNavigate   navigate  .
  
  const handleStartClick = () => {
    navigate('/random-list'); // 'random-list'  
  };
  
  useEffect(() => {
    //    --vh  
    const setVhVariable = () => {
      const vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
    };

    //      
    setVhVariable();

    //     --vh  
    window.addEventListener('resize', setVhVariable);

    //      
    return () => {
      window.removeEventListener('resize', setVhVariable);
    };
  }, []);
  return (
  <AppProvider>
    <div className="App">
      <div className="title">   <br/> ?</div>
      <div className='title-image-div'>
        <img className="title-image" src={process.env.PUBLIC_URL + "/img/titleImage.png"} alt="logo" />
      </div>
      <button className="start-button" onClick={handleStartClick}></button>
    </div>
  </AppProvider>
  );
}

export default App;
