import React from "react";
import "./Result.scss";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Result, results } from "@/constants";
import { useAppContext } from "@/components/AppContext";

import { TwitterShareButton, TwitterIcon } from "react-share";

const ResultPage = () => {
  const params = useParams();
  const { setNumbers, setSelectedAnswers, setCurrent } = useAppContext();
  const navigate = useNavigate();

  const clearLocalStorage = () => {
    //clear local storage
    localStorage.removeItem("randomNumbers");
    localStorage.removeItem("selectedAnswers");
    localStorage.removeItem("current");

    //clear states
    setNumbers([]);
    setSelectedAnswers([]);
    setCurrent(0);

    //navigate to home
    navigate("/");
  };

  const result = results[params.id as keyof Result];
  const usertype = params.id?.toLowerCase();
  return (
    <div className="result-page">
      <div className="result-div">
        <div className="result-image-div">
          <img
            className="result-image"
            src={process.env.PUBLIC_URL + "/img/" + usertype + ".jpg"}
            alt="logo"
          />
        </div>
        <div className="result-desc-div">{result.desc}</div>

        <div className="result-bottom-div">
          <div className="friends-div">
               
            <br />
            <div
              className="friends-image-div"
              onClick={() => (window.location.href = "./" + result.best)}
            >
              <img
                className="friends-image"
                src={
                  process.env.PUBLIC_URL +
                  "/img/" +
                  result.best.toLowerCase() +
                  ".jpg"
                }
                alt="logo"
              />
            </div>
          </div>
          <div className="friends-div">
              
            <br />
            <div
              className="friends-image-div"
              onClick={() => (window.location.href = "./" + result.worst)}
            >
              <img
                className="friends-image"
                src={
                  process.env.PUBLIC_URL +
                  "/img/" +
                  result.worst.toLowerCase() +
                  ".jpg"
                }
                alt="logo"
              />
            </div>
          </div>
        </div>
        <div className="bottom">
          <button className="bottom-button" onClick={clearLocalStorage}>
            
          </button>
          <TwitterShareButton
            url={window.location.href}
            title={
              "[   ]\n\n" +
              result.name +
              "\n\n" +
              result.desc
            }
            className="bottom-button"
          >
            <TwitterIcon size={32} round={true} />  
          </TwitterShareButton>
        </div>
      </div>
    </div>
  );
};

export default ResultPage;
